import React from 'react'

class InquireForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = { fName: '', lName: '', email: '', message: '' };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        alert('Form submitted: ' + this.state.email + this.state.message);
        event.preventDefault();
    }
    render() {
        return (
            <form className='flex flex-col items-center w-screen m-10' onSubmit={this.handleSubmit}>
                <input className="w-1/5 m-2 px-2 bg-chill-off-white" type="text" value={this.state.fName} onChange={this.handleChange} name="fName" placeholder='First' />
                <input className="w-1/5 m-2 px-2 bg-chill-off-white" type="text" value={this.state.lName} onChange={this.handleChange} name="lName" placeholder='Last' />
                <input className="w-3/5 m-2 px-2 bg-chill-off-white" type="text" value={this.state.email} onChange={this.handleChange} name="email" placeholder='Email' />
                <input className="w-3/5 m-2 px-2 bg-chill-off-white" type="text" value={this.state.message} onChange={this.handleChange} name="message" placeholder='Message' />
                <input type="submit" value="Submit" />
            </form>
        );
    }
}

export default InquireForm
